.intro {
  align-items: center;
  background-image: url(../../images/backgrounds/intro.jpg);
  background-size: cover;
  display: flex;
  height: 100%;

  .row {
    justify-content: center;

    h1 {
      color: #000;
      font-size: 60px;
      font-weight: 700;
      text-align: center;
      text-transform: uppercase;

      .brand {
        color: #e75926;
        text-shadow: 0 4px #a13e1a;
      }
    }

    p {
      color: #000;
      font-size: 20px;
      font-weight: 700;
      text-align: center;
    }

    svg {
      color: #000;
      font-size: 4em;
    }
  }

  .social {
    margin-top: 30px;

    > * {
      display: flex;
      justify-content: center;
    }
  }
}
